import {uniSegmentedControl} from '@dcloudio/uni-ui'  
export default {
	data() {
		return {
			items:[
				{tittle:'最新'},
				{tittle:'热门'},
				],
			current: 0,
			id:'',
			catelist:[],
			params:{
				limit:30,
				skip:0,
				order:'new'
			},
			orders:['new','hot'],
			hasmore:true
		}
	},
	components:{
		uniSegmentedControl
	},
	methods: {
		getlist(){
		this.request({
			url:`http://157.122.54.189:9088/image/v1/vertical/category/${this.id}/vertical`,
			data:this.params
		}).then(res=>{
			if(res.res.vertical.length!=0){
				this.catelist = [...this.catelist,...res.res.vertical];
			}else{
				uni.showToast({
					title:'没有图片了',
					icon:'none'
				})
				return;
			}
			
			
			
		})
			
		},
		chudi(){
			if(this.hasmore){	
			this.params.skip+=this.params.limit
			 this.getlist();
			 }
			 return;
		},
		onClickItem(e) {
		    if (this.current != e.currentIndex) {   //如果判断下标相等，说明用户点的还是当前页面，则不用换页。
		        this.current = e.currentIndex;
				this.params.order = this.orders[e.currentIndex]  //通过这里切换order。
				this.params.skip = 0
				this.catelist = []
				this.getlist()
			}
		}
	},
	onLoad(option) {//onload里面写一个参数就可以接收navagationl路由后面携带的参数
		this.id = option.id
		this.getlist();
		
	}
}
